.sidebar {
    background-color: var(--color-gray-dark-1);
    flex: 0 0 18%;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
}
.side-nav {
    font-size: 1.4rem;
    list-style: none;
    margin-top: 3.5rem;
    @media only screen and(max-width:900px) {
        display: flex;
        margin: 0;
    }
    &__item {
        position: relative;
        &:not(:last-child) {
            margin-bottom: 0.5rem;
            @media only screen and(max-width:900px) {
                margin-bottom: 0;
            }
        }
        @media only screen and(max-width:900px) {
            flex: 1;
        }
    }
    &__item::before {
        content: "";
        position: absolute;
        top: 0;
        left: 0;
        height: 100%;
        width: 3px;
        background-color: var(--color-primary);
        transform: scaleY(0);
        transition: transform 0.2s, width 0.4s cubic-bezier(1, 0, 0, 1) 0.2s, background-color 0.1s;
    }
    &__item:hover::before,
    &__item--active::before {
        transform: scaleY(1);
        width: 100%;
    }
    &__item:active::before {
        background-color: var(--color-primary-light);
    }
    &__link:link,
    &__link:visited {
        position: relative;
        color: var(--color-gray-light-1);
        text-decoration: none;
        text-transform: uppercase;
        display: block;
        padding: 1.5rem 3rem;
        display: flex;
        align-items: center;
        z-index: 20;
        @media only screen and(max-width:900px) {
            justify-content: center;
            padding: 2rem;
        }
        @media only screen and(max-width:600px) {
            flex-direction: column;
            padding: 1.5rem 0.5rem;
        }
    }
    &__icon {
        width: 1.75rem;
        height: 1.75rem;
        fill: currentColor;
        margin-right: 2rem;
        @media only screen and(max-width:600px) {
            width: 1.5rem;
            height: 1.5rem;
            margin-right: 0;
            margin-bottom: 0.7rem;
        }
    }
}
.legal {
    font-size: 1.2rem;
    text-align: center;
    padding: 2.5rem;
    color: var(--color-gray-light-4);
    @media only screen and(max-width:900px) {
        display: none;
    }
}
